const DAY = 24 * 3600 * 1000;
const WEEK = 7 * DAY;

/**
 * 
 */
export const MODE = {
  date: 'date', // 
  week: 'week', // 
  range: 'range', // 
};

/**
 * weekModemode&
 * @param {MODE} mode
 * @param {boolean} weekMode
 */
export function getMode(mode, weekMode) {
  return weekMode ? MODE.week : mode;
}

/**
 * 
 * @param {MODE} mode
 */
export function isRangeMode(mode) {
  return [MODE.week, MODE.range].indexOf(mode) >= 0;
}

/**
 * 
 * @param {*} mode
 * @param {*} selected
 * @param {*} range
 */
export function isWaitForSecondDay(mode, selected, range) {
  return !!(mode === MODE.range && selected && !range);
}

/**
 * 
 * @param  {Date} day 
 * @param {Number} offset 
 * @return {Date}     
 */
export function getDayOfMonth(day, offset = 1) {
  let firstDate = new Date(day);

  firstDate.setDate(offset);
  return firstDate;
}

/**
 * 
 * @param {Date} day       
 * @param {Number} count 
 */
export function addDayWithWeeks(day, count = 1) {
  const dayTimeValue = day.getTime();

  return new Date(dayTimeValue + count * WEEK);
}

/**
 * 
 * @param {Date} day   
 * @param {Number} count 
 */
export function addDay(day, count = 1) {
  const dayTimeValue = day.getTime();

  return new Date(dayTimeValue + count * DAY);
}

/**
 * 
 * @param  {Date} day 
 * @param  {Number} weekTarget  0 - 6(-)
 * @return {[type]}     
 */
export function getDayOfWeek(day, weekTarget = 0) {
  const dayTimeValue = day.getTime();
  const weekOrigin = day.getDay();
  let reDate;

  reDate = new Date(dayTimeValue + (weekTarget - weekOrigin) * DAY);

  return reDate;
}

/**
 * 
 * @param  {Date}  day        
 * @param  {Date}  dayOfMonth 
 * @return {Boolean}            
 */
export function isDayInMonth(day, dayOfMonth) {
  const firstDayofMonthValue = getDayOfMonth(dayOfMonth, 1).getTime();

  return getDayOfMonth(day, 1).getTime() === firstDayofMonthValue;
}

/**
 * 
 * @param  {Date}  dayOfWeek  
 * @param  {Date}  dayOfMonth 
 * @return {Boolean}            
 */
export function isWeekInMonth(dayOfWeek, dayOfMonth) {
  const firstDayOfWeek = getDayOfWeek(dayOfWeek, 0);
  const lastDayofWeek = getDayOfWeek(dayOfWeek, 6);

  return isDayInMonth(firstDayOfWeek, dayOfMonth) ||
    isDayInMonth(lastDayofWeek, dayOfMonth);
}

/**
 * 0:00:00,
 * @param  {Date|Number} day 
 * @return {Date}     
 */
export function getPureDate(day) {
  if (day === undefined || day === null) {
    return undefined;
  }

  if (day.constructor !== Date) {
    day = new Date(day);
  }

  const year = day.getFullYear();
  const month = day.getMonth();
  const date = day.getDate();

  return new Date(year, month, date);
}

/**
 * 1
 * @param  {Date} day 
 * @return {Date}     1
 */
export function getDayOfNextMonth(day) {
  return getDayOfMonth(getDayOfMonth(day, 32), 1);
}

/**
 * 1
 * @param  {Date} day 
 * @return {Date}     1
 */
export function getDayOfLastMonth(day) {
  return getDayOfMonth(getDayOfMonth(day, 0), 1);
}

/**
 * 
 * @param  {Date} day    
 * @param  {Number} offset 
 * @return {Date}        
 */
export function getDayOfOffsetYear(day, offset = 0) {
  return day && new Date(day.getFullYear() + offset, 0, 1);
}

/**
 * 
 * @param  {Date}  dayA 
 * @param  {Date}  dayB 
 * @return {Boolean}      
 */
export function isSameDate(dayA, dayB) {
  return dayA && dayB &&
    getPureDate(dayA).getTime() === getPureDate(dayB).getTime();
}

/**
 * 
 * @param  {Date}  dayA 
 * @param  {Date}  dayB 
 * @return {Number}      
 */
export function compareDate(dayA, dayB) {
  return getPureDate(dayA).getTime() - getPureDate(dayB).getTime();
}

/**
 * 
 * @param  {Date} day    
 * @param  {String} format  YYYYMMDD
 * @return {String}        
 */
export function formatDateStr(day, format = 'YYYY-MM-DD') {
  if (day === undefined) {
    day = new Date();
  }

  if (day.constructor !== Date) {
    day = new Date(day);
  }

  const year = day.getFullYear();
  const month = day.getMonth() + 1;
  const date = day.getDate();

  const fillZero = (num) => {
    if (num >= 0 && num < 10) {
      return `0${num}`;
    }
    return num;
  };

  return format.replace('YYYY', year)
    .replace('MM', fillZero(month))
    .replace('DD', fillZero(date))
    .replace('M', month)
    .replace('D', date);
}

/**
 * 
 * @param {*} day 
 * @param {object} range 
 * @return {Boolean}       
 */
export function isDateInRange(day, range) {
  if (!range) {
    return false;
  }
  const { from, to } = range;
  return compareDate(day, from) >= 0 && compareDate(day, to) <= 0;
}

/**
 * 
 * @param  {...Date} days 
 * @return {[type]}         [description]
 */
export function getMaxDate(...days) {
  return days.filter((day) => {
    return day;
  }).reduce((maxDay, day) => {
    return (maxDay && compareDate(maxDay, day)) >= 0 ? maxDay : day;
  }, undefined);
}

/**
 * 
 * @param  {...Date} days 
 * @return {Date}      
 */
export function getMinDate(...days) {
  return days.filter((day) => {
    return day;
  }).reduce((minDay, day) => {
    return (minDay && compareDate(minDay, day)) <= 0 ? minDay : day;
  }, undefined);
}

/**
 * 
 * @param {*} dates 
 * @param {*} date 
 */
export function findDate(dates, date) {
  for (let i = 0; i < dates.length; i++) {
    if (isSameDate(dates[i], date)) {
      return true;
    }
  }
  return false;
}
