import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import MessageItem from './MessageItem';
import PropTypes from 'prop-types';

/**
 * 
 *  tiptiptip
 * 
 */

class Message extends Component {
  static propTypes = {
    /**
     *   	React.Element or String
     */
    content: PropTypes.oneOfType([
      PropTypes.document,
      PropTypes.string,
    ]),
    /**
     * 	loading0
     */
    duration: PropTypes.number,
    /**
     * , false
     */
    closable: PropTypes.bool,
    /**
     * 	
     */
    onClose: PropTypes.func,
    /**
     * 
     */
    direction: PropTypes.oneOf(['top', 'bottom', 'center', 'left', 'right']),
  }

  static defaultProps = {
    closable: false,
    duration: 1.5,
    direction: 'top',
  }

  state = {
    // 
    messages: [],
  };

  // item
  add = (newMessage) => {
    let messages = this.state.messages;
    // key
    let hasSomeKey = messages.filter(m => m.key === newMessage.key).length;
    if (!hasSomeKey) {
      this.setState({
        messages: messages.concat(newMessage),
      });
    }
  };

  // item
  remove = (key) => {
    // 
    let newMessages = this.state.messages.filter(m => m.key !== key);
    this.setState({
      messages: newMessages,
    });
  };


  render() {
    // message
    let messageNodes = this.state.messages.map(message => {
      return (
        <MessageItem
          {...message}
          mKey={message.key}
          onRemove={this.remove}
        >
          {message.content}
        </MessageItem>
      );
    });
    return (
      <div className="im-message" style={{
        zIndex: this.zIndex,
      }}>
        {messageNodes}
      </div>
    );
  }
}

// Message
Message.init = function (properties) {
  const props = properties || {};
  const div = document.createElement('div');
  div.className = 'im-message-wrapper';
  document.body.appendChild(div);
  const message = ReactDOM.render(<Message {...props} />, div);

  return {
    // messageItem
    add: function notice(messageProps) {
      message.add(messageProps);
    },
    // keymessageItem
    remove: function removeNotice(key) {
      message.remove(key);
    },
    // componet
    component: message,
    // message
    destroy: function destroy() {
      ReactDOM.unmountComponentAtNode(div);
      document.body.removeChild(div);
    },
  };
};

export default Message;
