import React from "react";
import { FixedSizeList as List } from "react-window";
import InfiniteLoader from "react-window-infinite-loader";
import DropdownOption from './DropdownOption';

// https://github.com/bvaughn/react-window-infinite-loader/
export default function DropdownInfiniteOptions({
  /** 
   *  
   */
  hasNextPage,

  /**
   * 
   */
  isNextPageLoading,

  /**
   * Dropdownoptions
   */
  items,

  /**
   * 
   */
  loadNextPage,
  /** 
   *  150
   */
  listHeight = 150,
  /** 
   * 30
   */
  itemSize = 30,
}) {
  // 
  let itemCount = hasNextPage ? items.length + 1 : items.length;

  // 
  const loadMoreItems = isNextPageLoading ? () => {} : loadNextPage;

  // itemcount
  const isItemLoaded = index => !hasNextPage || index < items.length ;

  const height = items.length === 0 ? 0 : listHeight;

  const Item = ({ index, style }) => {
    let values = items[index];
    let content;

    if (!isItemLoaded(index)) {
      content = <div className="im-dropdown-infinite-option-item_loading">Loading...</div>;
    } else {
      content = <DropdownOption
                  key={values.id} 
                  option={values}
                >
                  { values.name || ''}
                </DropdownOption>
    }

    return (
      <div className="im-dropdown-infinite-option-item" style={style}>
        {
          content
        }
      </div>
    )
  };

  return (
    <InfiniteLoader
      className="im-dropdown-infinite-option"
      isItemLoaded={isItemLoaded}
      itemCount={itemCount}
      loadMoreItems={loadMoreItems}
    >
      {({ onItemsRendered, ref }) => (
        <List
          height={height}
          itemCount={itemCount}
          itemSize={itemSize}
          onItemsRendered={onItemsRendered}
          ref={ref}
        >
          {Item}
        </List>
      )}
    </InfiniteLoader>
  );
}
